const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			addressList: []
		}
	},
	//初始化
	onLoad() {
		
	},
	// 页面显示
	onShow() {
		this.getList();
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取收货地址
		getList() {
			util.request(api.getAddreList,{},'get').then(res=>{
				if(res.code == 0){
					this.addressList = res.data.list;
				}
			})
		},
		
		//删除地址
		deleteAddress(id){
			wx.navigateTo({
				url: '/pages/user/editAddress?id=' + id
			})
		},
		
		//新增
		addressAddOrUpdate() {
			wx.navigateTo({
				url: '/pages/user/editAddress'
			})
		},
		// 设为默认
		setIsDefault(id) {
			util.request(api.setAddrDefault,{addr_id: id},'post').then(res=>{
				if(res.code == 0) {
					this.getList()
				}
			})
		},
		addressAddOrder(id){
			
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
